import styles from './style.module.css';
import { initProfileFromServer, editProfileFromServer } from '../../redux/actions/userProfile.action';
import { useDispatch, useSelector } from 'react-redux';
import React, { useEffect } from 'react';

const months = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
];
const years = ['2018', '2019', '2020', '2021', '2022', '2023', '2024', '2025', '2026'];
const ampus = ['', '-', ''];

function EditProfile(props) {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.profile);

  useEffect(() => {
    dispatch(initProfileFromServer());
  }, []);

  function hendleSubmit(event) {
    event.preventDefault();
    const form = event.target;
    const data = Object.fromEntries(new FormData(form));
    dispatch(editProfileFromServer(data))
  }
  return (
    <div >
      {user && (
        <div className='login-form'>
          <form onSubmit={hendleSubmit}>
            <input
              type="text"
              name="name"
              placeholder=""
              defaultValue={user.name}
              autoComplete="off"
            />
            <input
              type="text"
              name="surName"
              placeholder=""
              defaultValue={user.surName}
              autoComplete="off"
            />

            <h3 className={styles.label}> </h3>
            <input
              type="text"
              name="currentCountryName"
              placeholder=""
              defaultValue={user['currentCou.countryName']}
              autoComplete="off"
            />
            <input
              type="text"
              name="currentCityName"
              placeholder=""
              defaultValue={user['currentCit.cityName']}
              autoComplete="off"
            />
            <h3 className={styles.label}></h3>
            <input
              type="text"
              name="telegram"
              defaultValue={user.telegram}
              placeholder="Telegram"
            />
            <input type="text" name="github" defaultValue={user.github} placeholder="GitHub" />
            <h3 className={styles.label}> </h3>

            <select name="campusName" >
              {!user['Campus.campusName'] && <option disabled selected></option>}
              {ampus.map((el, i) => (<option key={i} selected={!user['Campus.campusName'] === el}>{el}</option>))}
            </select>

            <select name="yearFinishDate" >
              {!user.yearFinishDate && <option disabled selected></option>}
              {years.map((el, i) => (<option key={i} selected={user?.yearFinishDate === el}>{el}</option>))}
            </select>
            <select name="monthFinishDate" >
              {!user.monthFinishDate && <option disabled selected></option>}
              {months.map((el, i) => (<option key={i} selected={user?.monthFinishDate === el}>{el}</option>))}
            </select>
            <h3 className={styles.label}>  </h3>
            <input
              type="text"
              name="countryName"
              placeholder=""
              defaultValue={user['Country.countryName']}
              autoComplete="off"
            />
            <input
              type="text"
              name="cityName"
              placeholder=""
              defaultValue={user['City.cityName']}
              autoComplete="off"
            />
            <h3 className={styles.label}> </h3>
            <input
              type="date"
              name="dateOfBirth"
              min="1950-01-01"
              max="2007-12-31"
              defaultValue={user.dateOfBirth}
            />

            <input type="submit" defaultValue="" />
          </form>
        </div>
      )}
    </div>
  );
}

export default EditProfile;
