import { useDispatch } from 'react-redux';
import { userLogin } from '../../redux/actions/auth';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux'
import { useEffect } from 'react'
import Error from '../Error/Error'
import { Link } from 'react-router-dom';


export default function Login() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { error, values, isLoading } = useSelector(state => state.user)

  useEffect(() => {
    if (values.id) {
      navigate('/home')
    };
  }, [values])

  const handleSubmit = (event) => {
    event.preventDefault();
    const payload = {
      email: event.target.email.value,
      password: event.target.password.value,
    };

    dispatch(userLogin(payload));
  };

  return (

    <div className='login-form'>
      <div className='nav-login-registration'>
        <div className='link-nav link-nav--active' >
          <Link to="/login" className='link'>
            <h3 className='h3-link'></h3>
          </Link>
        </div>
        <div className='link-nav'>
          <Link to="/registration" className='link'>
            <h3 className='h3-link'></h3>
          </Link>
        </div>
      </div>
      <form onSubmit={handleSubmit}>
        <input type="text" name="email" placeholder="Email" autoFocus autoComplete="off" />
        <input type="password" name="password" placeholder="" autoComplete="off" />
        <button className="login-form-button" type="submit" disabled={isLoading}>{isLoading ? '...' : ''}</button>
        {error && <Error error={error.error} />}
      </form>
    </div>
  );
}
