import axios from 'axios';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

// const campuses = ['', '-', ''];

function AddMainInfo() {
  const navigate = useNavigate()
  const [inputedData, setInputedData] = useState('')
  useEffect(() => {
    const dataForm = JSON.parse(localStorage.getItem('addedInfo'))
    setInputedData(dataForm)
  }, [])

  async function addInfo(event) {
    event.preventDefault()
    const form = event.target;
    const dataForm = Object.fromEntries(new FormData(form));
    localStorage.setItem('addedInfo', JSON.stringify(dataForm))
    navigate('/moreInfo')
  }

  return (
    <div className='login-form'>
      <form onSubmit={addInfo}>
        <div>  !</div>
        <input type="text" defaultValue={inputedData?.name} name="name" placeholder="*" autoFocus autoComplete="off" required></input>
        <input type="text" defaultValue={inputedData?.surName} name="surName" placeholder="" autoComplete="off"></input>
        <div>  </div>
        <input type="text" defaultValue={inputedData?.countryName} name="countryName" placeholder="*" autoComplete="off" required></input>
        <input type="text" defaultValue={inputedData?.cityName} name="cityName" placeholder="*" autoComplete="off" required></input>
        <select name="campusName" >
          {inputedData ? <option>{inputedData?.campusName}</option> : <option disabled selected></option>}
          {inputedData?.campusName !== '' && <option></option>}
          {inputedData?.campusName !== '-' && <option>-</option>}
          {inputedData?.campusName !== '' && <option></option>}
        </select>
        <p className='post-scriptum-necessarily'>*  </p>
        {/* <select defaultValue={inputedData?.campusName} name="campusName" >
          {!inputedData.campusName && <option disabled selected></option>}
          {campuses.map(el => (<option selected={inputedData?.campusName === el}>{el}</option>))}
        </select> */}
        <button className='registration-form-button'></button>
      </form>
    </div>
  );
}

export default AddMainInfo;
